.gpt3__cta{
    border:1px solid white;
    display:flex;
    flex-direction:row;
    justify-content:space-between;
    align-items:center;
    /* color:#fff; */
    flex-direction:row;
    padding:2rem;
    border-radius:1rem;
    background:var(--gradient-bar)
}
.gpt3__cta-content p{
    font-family: var(--font-family);
    font-size: 12px;
    font-weight: 500;
    line-height: 30px;
    

}
.gpt3__cta-content h4{
    font-family: var(--font-family);
    font-size: 24px;
    font-weight: 800;
    line-height: 45px;

}
.gpt3__cta-btn{
    display:flex;
    justify-content:center;
    align-items:center;
    margin-left:2rem;
}
.gpt3__cta-btn button{
    color:#fff;
    border-Radius:40px;
    font-family: var(--font-family);
    font-size: 18px;
    font-weight: 700;
    line-height: 30px;
    padding:0.5rem 1rem;
    background: #000000;
    border:none;
    outline:none;
    cursor:pointer;
    min-width:150px;

}

@media screen and (max-width:650px){
    .gpt3__cta{
        flex-direction:column;
    }
    .gpt3__cta-btn{
        margin:2rem 0 0;
    }
}
@media screen and (max-width:550px){

    .gpt3__cta{
        margin: 4rem 2rem;
    }
    .gpt3__cta-content h4{
        font-size:18px;
        line-height:32px;
    }
    .gpt3__cta-btn button{
        font-size:14px;
        line-height:28px;
    }
}



