.gpt3__header{
    display:flex;
}

.gpt3__header-content{
    flex:1;
    display:flex;
    flex-direction:column;
    justify-content:center;
    align-items:flex-start;
    margin-right:5rem;
}

.gpt3__header-content h1{
    font-family:var(--font-family);
    font-size: 62px;
    font-weight: 800;
    line-height: 75px;
    letter-spacing: -0.04em;

}
.gpt3__header-content p{
    font-family:var(--font-family);
    font-size: 18px;
    font-weight: normal;
    font-style:normal;
    line-height: 25.32px;
    color: var(--color-text);
    margin-top:1.5rem
}
.gpt3__header-content__input{
    display:flex;
    width:100%;
    margin:2rem 0 1rem;
}
.gpt3__header-content__input input{
    flex:2;
    width:100%;
    min-height:50px;
    background:var(--color-footer);
    font-family:var(--font-family);
    font-size: 20px;
    line-height: 27.32px;
    padding: 0 1rem;
    outline:none;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    border:2px solid var(--color-footer);
    color: #fff;
    text-align: left;

}
.gpt3__header-content__input button{
    flex:0.6;
    width:100%;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    background:#FF4820;
    font-family:var(--font-family);
    font-size: 20px;
    font-weight: bold;
    line-height: 27.32px;
    color: #fff;
    outline:none;
    cursor:pointer;
    padding: 0 1rem;
    border:none;
}
.gpt3__header-content__people{
    display:flex;
    width:100%;
    justify-content:flex-start;
    align-items:center;

    margin-top:2rem;
}
.gpt3__header-content__people img{
    width: 181.79px;
    height: 38px;
    }
.gpt3__header-content__people p{
    margin:1rem;
    font-family:var(--font-family);
    font-size: 12px;
    font-weight: 500;
    line-height: 37.61px;
    text-align: left;

}
.gpt3__header-image {
    flex:1;
    display:flex;
    justify-content:center;
    align-items:center;
    margin:0px;
    padding:0px;

}

.gpt3__header-image img{
    width:100%;

}

@media screen and (max-width : 1050px){
    .gpt3__header{
        flex-direction:column;
    }

    .gpt3__header-content{
        margin: 0 0 3rem;
    }
}
@media screen and (max-width : 650px){
    .gpt3__header-content h1{
       font-size:48px;
       line-height:60px
    }

    .gpt3__header-content p{
        font-size:16px;
        line-height:24px;
    }
    .gpt3__header-content__people{
        flex-direction:column;
    }
    .gpt3__header-content__people p{
        margin:0;
    }
    .gpt3__header-content__input input,
    .gpt3__header-content__input button{
        font-size:16px;
        line-height:24px;
    }
}
@media screen and (max-width : 490px){
    .gpt3__header-content h1{
        font-size:36px;
        line-height:40px
     }
 
     .gpt3__header-content p{
         font-size:14px;
         line-height:22px;
     }
     .gpt3__header-content__input input,
    .gpt3__header-content__input button{
        font-size:12px;
        line-height:16px;
    }
}