.gpt3__features-container__feature{
    width:100%;
    display:flex;
    flex-direction:row;
    justify-content:space-between;
    align-items:flex-start;

    margin:1rem;
}

.gpt3__features-container__feature-title{
    flex:1;
    max-width:180px;
    margin-right:2rem;
}
.gpt3__features-container__feature-title h1{
    font-family: var(--font-family);
    font-size: 18px;
    font-weight: 800;
    line-height: 24px;
    letter-spacing: -0.04em;
    /* text-align: left; */
    color:#fff;

}
.gpt3__features-container__feature-title div{
    width: 38px;
    height: 3px;
    background:var(--gradient-bar);
    box-shadow: 0px 4px 4px rgb(0,0,0,0.25);
    margin-bottom:0.5rem;
}
.gpt3__features-container__feature-text{
    flex:2;
    max-width:590px;
    display:flex;
}

.gpt3__features-container__feature-text p{
    font-family: var(--font-family);
    font-size: 14px;
    font-weight: 500;
    line-height: 24px;
    color:var(--color-text);

}

@media screen and (max-width: 550px){
    .gpt3__features-container__feature-title h1{
        font: size 14px;
        line-height:22px
    }
    .gpt3__features-container__feature-text p{
        font-size:12px;
        line-height:20px;
    }
    .gpt3__features-container__feature{
        margin:1rem 0;
    }
}