.gpt3__footer{
    display:flex;
    flex-direction:column;


    justify-content:center;
    align-items:center;
    background: var(--color-footer)
}
.gpt3__footer-heading{
    width:100%;
    text-align:center;

    margin-bottom:3rem;
}

.gpt3__footer-heading h1{
    font-family: var(--font-family);
    font-size: 62px;
    font-weight: 800;
    line-height: 75px;
    letter-spacing: -0.04em;
    text-align: center;

}
.gpt3__footer-btn{
    border:1px solid #fff;
    display:flex;
    justify-content:center;
    align-items:center;

    padding:1rem;
    text-align:center;
    margin-bottom:10rem;
    cursor:pointer;

}
.gpt3__footer-btn p{
    font-family: var(--font-family);
    font-size: 18px;
    line-height: 21.09px;
    word-spacing:2px;
    color:#fff;

}

.gpt3__footer-links{
    display:flex;
    flex-direction:row;
    flex-wrap:wrap;
    justify-content:space-between;
    align-items:flex-start;
    text-align:left;
    width:100%;

}
.gpt3__footer-links div{
    width:250px;
    margin:1rem;

}
.gpt3__footer-links_logo{
    display:flex;
    flex-direction:column;
}
.gpt3__footer-links_logo img{
    width: 118px;
    height: 30px;

    margin-bottom:1rem;

}

.gpt3__footer-links_logo p{
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 14.06px;
    text-align: left;
    color:#fff;
}
.gpt3__footer-links_div{
    display:flex;
    flex-direction:column;
    justify-content:flex-start;

}
.gpt3__footer-links_div h4{
    font-family: var(--font-family);
    font-size: 14px;
    line-height: 16.41px;
    text-align: left;
    margin-bottom:1rem;
    color:#fff;
    
}
.gpt3__footer-links_div p{
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 14.06px;
    text-align: left;
    margin:0.5rem 0;
    color:#fff;

}
.gpt3__footer-copyrights{
    width:100%;
    text-align:center;
    margin-top:2rem;
}
.gpt3__footer-copyrights p{
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 14.06px;
    color:#fff;
}


@media screen and (max-width:850px){
    .gpt3__footer-heading h1{
        font-size:44px;
        line-height:50px;
    }
}

@media screen and (max-width:550px){
    .gpt3__footer-heading h1{
        font-size:34px;
        line-height:42px;
    }
    .gpt3__footer-links div{
        margin:1rem 0;
    
    }
    .gpt3__footer-btn p{
        font-size:14px;
        line-height:20px
    }
}

@media screen and (max-width:550px){
    .gpt3__footer-heading h1{
        font-size:27px;
        line-height:38px;
    }
}