.gpt3__navbar{
    display:flex;
    justify-content:space-between;
    align-items:center;
    padding: 2rem  6rem;
}
.gpt3__navbar-links{
    flex:1;
    display:flex;
    justify-content:flex-start;
    align-items:center;
}
.gpt3__navbar-links_logo{
    margin-right:2rem;
}
.gpt3__navbar-links_logo img{
    height:16.02px;
    width:62.56px;
}
.gpt3__navbar-links_containers{
    display:flex;
    flex-direction:row;
}
.gpt3__navbar-sign{
    display:flex;
    justify-content:flex-end;
    align-items:center;
}
.gpt3__navbar-links_containers p,
.gpt3__navbar-sign p,
.gpt3__navbar-menu_containers-links p,
.gpt3__navbar-menu_containers-links-sign p{
    color:#fff;
    font-family:var(--font-family);
    font-weight:500;
    font-size:18px;
    line-height:25px;
    text-transform:capitalize;
    margin:0 1rem;
    cursor:pointer;
}
.gpt3__navbar-sign button,
.gpt3__navbar-menu_containers-links-sign button{
    padding: 0.5rem 2rem;

    font-family:var(--font-family);
    font-weight:500;
    font-size:18px;
    line-height:25px;
    background-color:#FF4820;
    color:#fff;
    border:none;
    outline:none;
    cursor:pointer;
}
.gpt3__navbar-menu{
    margin-left:1rem;
    display:none;
    position:relative;
}
.gpt3__navbar-menu svg{
    cursor:pointer;
}
.gpt3__navbar-menu_conatiners{
    display:flex;
    justify-content:flex-end;
    align-items:flex-end;
    flex-direction:column;
    text-align:end;
    background:var(--color-footer);
    padding:2rem;
    position:absolute;
    top:80px;
    right:0;
    margin-top:1rem;
    min-width:210px;
    border-radius:5px;
    box-shadow:0 0 5 black;
}
.gpt3__navbar-menu_containers p{
    margin:1rem 0;
}
.gpt3__navbar-menu_containers-links-sign{
    display:none;
}

/* for tablets */
@media screen and (max-width: 1050px){
    .gpt3__navbar-links_containers{
        display:none;
    }
    .gpt3__navbar-menu{
        display:flex;
    }
}


@media screen and (max-width: 700px){
    .gpt3__navbar{
        padding:2rem 4rem;
    }
}
@media screen and (max-width: 550px){
    .gpt3__navbar{
        padding:2rem;
    }
    .gpt3__navbar-sign{
        display:none;
    }
    .gpt3__navbar-menu_containers-links-sign{
        display:block;
    }
}